import React from "react";
import "./styles.css";
const ListItems = ({ items }) => {
  //   console.log("items", items);

  return (
    <ul className="collection">
      {items.map(item => (
        <li key={item} className="collection-item">
          {item.driver_id}
        </li>
      ))}
    </ul>
  );
};

export default ListItems;
